<template>
  <div class="info_table">
    <el-row>
      <el-col :span="5"><span>{{}}</span></el-col>
      <el-col :span="5"><span>{{}}</span></el-col>
      <el-col :span="5"><span>{{}}</span></el-col>
      <el-col :span="5"><span>{{}}</span></el-col>
      <el-col :span="4"><span><span>&nbsp;</span></span></el-col>
    </el-row>
    <el-row>
      <el-col :span="5"><span>{{baseInfo.debtor_eduction}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_gender}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_cert_type + ':' + baseInfo.debtor_cert_number}}</span></el-col>
      <el-col :span="9"><span>{{baseInfo.debtor_permanent_addresses}}</span> <i @click="updatePermanent" class="jf-icon el-icon-edit"></i></el-col>
    </el-row>
    <el-row>
      <el-col :span="5"><span>{{baseInfo.debtor_permanent_addresses}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_marital_status}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_eduction}}</span></el-col>
      <el-col :span="9"><span>{{baseInfo.debtor_residence_addresses}}</span> <i @click="updateResidence" class="jf-icon el-icon-edit"></i></el-col>
    </el-row>
    <el-row>
      <el-col :span="5">/<span>{{baseInfo.debtor_residence_addresses}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_industry}}</span></el-col>
      <el-col :span="5"><span>{{baseInfo.debtor_onthly_income}}</span></el-col>
      <el-col :span="9">/<span>{{baseInfo.debtor_company_addresses}}</span> <i @click="updateCompany" class="jf-icon el-icon-edit"></i></el-col>
    </el-row>
    <el-row class="mul-row">
      <el-col :class="{'mul-col': baseInfo.debtor_bank_account_vo_list.debtor_bank_account.length > 1}" :span="12">
        <el-row type="flex">
          <div class="info-label"></div>
          <div class="info-detail">
            <div v-for="(bank, index) in baseInfo.debtor_bank_account_vo_list.debtor_bank_account">{{bank}}
              <el-button @click="updateBank(index)" type="text"></el-button>
            </div>
            <div class="flex-row">
              <el-button size="small" type="primary" @click="addBank">
              </el-button>
            </div>
          </div>
        </el-row>
      </el-col>
      <el-col :class="{'mul-col': baseInfo.debtor_mobile_vo_list.length > 1}" :span="12">
        <el-row type="flex">
          <div></div>
          <div>
            ..<div v-for="(mobile, index) in baseInfo.debtor_mobile_vo_list">{{mobile.debtor_mobile}}
              <el-tag type="primary">{{mobile.status | mobileFilter}}</el-tag>
              <el-button @click="deleteMobile(index)" type="text"></el-button>
              <el-button @click="updateMobile(index)" type="text"></el-button>
              <el-button @click="updateStatus(index)" type="text"></el-button>
            </div>
            <div class="flex-row">
              <el-input v-show="showMobileInput" size="small" type="text" v-model="debtorMobileNum"></el-input>
              <el-button v-if="!showMobileInput" size="small" type="primary" @click="showMobileInput = true">
              </el-button>
              <el-button size="small" type="primary" @click="addMobile" v-else></el-button>
            </div>
          </div>
        </el-row>
      </el-col>
      <!--<el-col :span="5"><span>{{baseInfo.debtor_industry}}</span></el-col>-->
      <!--<el-col :span="4"><span>&nbsp;</span></el-col>-->
    </el-row>
    <el-dialog title="" size="tiny" :modal-append-to-body="false" :modal="false" :visible.sync="mobileVisible">
      <el-form>
        <el-form-item>
          <el-select v-model="mobileStatus" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="mobileVisible = false"> </el-button>
        <el-button type="primary" @click="updateStatusSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import debtor from '@/api/debtor'
  export default {
    props: ['debtorId', 'contactId'],
    data () {
      return {
        baseInfo: {
          debtor_name: '',
          debtor_gender: '',
          debtor_eduction: '',
          debtor_graduate: '',
          debtor_marital_status: '',
          debtor_revenue_source: '',
          debtor_industry: '',
          debtor_onthly_income: '',
          debtor_cert_type: '',
          debtor_cert_number: '',
          debtor_permanent_addresses: '',
          debtor_company_addresses: '',
          debtor_residence_addresses: '',
          debtor_bank_account_vo_list: {
            debtor_bank_account: ['6221887021016084238', '6221887021016084239', '6221887021016084237']
          },
          debtor_bank_account: '',
          debtor_mobile_vo_list: [
            {status: 0, debtor_mobile: 18756230700},
            {status: 0, debtor_mobile: 18756230700}
          ],
          state: '',
          debtor_mobile: ''
        },
        showMobileInput: false,
        debtorMobileNum: '', // 
        mobileStatus: '', // 
        updatedMobileIdx: '',
        mobileVisible: false
      }
    },
    methods: {
      showBasicDebtorInfo () {
        debtor.showBasicDebtorInfo({
          debtorId: '',
          contactId: ''
        }).then(res => {
          let data = res.data
          if (data.code === 1000) {

          }
        }).catch()
      },
      deleteMobile (index) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.baseInfo.debtor_mobile_vo_list.splice(index, 1)
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      addMobile () {
        debtor.addDebtorMobile({
          debtorId: this.debtorId,
          contractId: this.contractId,
          debtorMobileNum: this.debtorMobileNum
        }).then(res => {
          this.baseInfo.debtor_mobile_vo_list.push({status: 0, debtor_mobile: this.debtorMobileNum})
          this.debtorMobileNum = ''
          this.showMobileInput = false
        }).catch()
      },
      updateMobile (index) {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^1[3|4|5|7|8][0-9]{9}$/,
          inputErrorMessage: ''
        }).then(({value}) => {
          // 
          debtor.addDebtorMobile({
            debtorId: this.debtorId,
            contractId: this.contractId,
            oldDebtorMobile: this.baseInfo.debtor_mobile_vo_list[index].debtor_mobile,
            newDebtorMobile: value
          }).then(res => {
            let data = res.data
            if (data.code === '1000') {
              this.$message({
                type: 'success',
                message: ''
              })
              console.info(data)
            }
          }).catch(() => {
            this.$message.error('')
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      updateStatus (idx) {
        this.updatedMobileIdx = idx
        this.mobileStatus = this.baseInfo.debtor_mobile_vo_list[idx].status
        this.mobileVisible = true
      },
      updateStatusSubmit () {
        debtor.modifyDebtorMobileState({
          debtorId: this.debtorId,
          contractId: this.contractId,
          debtorMobileNum: this.baseInfo.debtor_mobile_vo_list[this.updatedMobileIdx].debtor_mobile,
          state: this.mobileStatus
        }).then(res => {
          let data = res.data
          if (data.code === '1000') {
            console.info(data)
          }
        }).catch(() => {
          this.$message.error('')
        })
      },
      addBank () {
        this.arouBank(true, '')
      },
      updateBank (idx) {
        this.arouBank(false, this.baseInfo.debtor_bank_account_vo_list.debtor_bank_account[idx])
      },
      arouBank (isAdd, oldDebtorBankAccount) {
        let arouInfo = isAdd ? '' : ''
        this.$prompt(oldDebtorBankAccount ? '' + oldDebtorBankAccount : '', arouInfo + '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^(\d{16}|\d{19})$/,
          inputErrorMessage: ''
        }).then(({value}) => {
          // 
          let res = ''
          let params = {
            debtorId: this.debtorId,
            contractId: this.contractId,
            newDebtorBankAccount: value
          }
          if (isAdd) {
            res = debtor.addDebtorBankAccount()
          } else {
            res = debtor.modifyDebtorBankAccount()
            params.oldDebtorBankAccount = oldDebtorBankAccount
          }
          res.then(res => {
            let data = res.data
            if (data.code === '1000') {
              this.$message({
                type: 'success',
                message: arouInfo + ''
              })
              console.info(data)
            }
          }).catch(() => {
            this.$message.error(arouInfo + '')
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: `${arouInfo}`
          })
        })
      },
      updatePermanent () {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({value}) => {
          // 
          debtor.modifyDebtorPermanentAddress({
            debtorId: this.debtorId,
            contractId: this.contractId,
            newDebtorAddresses: value
          }).then(res => {
            let data = res.data
            if (data.code === '1000') {
              this.$message({
                type: 'success',
                message: ''
              })
              console.info(data)
            }
          }).catch(() => {
            this.$message.error('')
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      updateCompany () {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({value}) => {
          // 
          debtor.modifyDebtorCompanyAddress({
            debtorId: this.debtorId,
            contractId: this.contractId,
            newDebtorAddresses: value
          }).then(res => {
            let data = res.data
            if (data.code === '1000') {
              this.$message({
                type: 'success',
                message: ''
              })
              console.info(data)
            }
          }).catch(() => {
            this.$message.error('')
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      updateResidence () {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({value}) => {
          // 
          debtor.modifyDebtorResidenceAddress({
            debtorId: this.debtorId,
            contractId: this.contractId,
            newDebtorAddresses: value
          }).then(res => {
            let data = res.data
            if (data.code === '1000') {
              this.$message({
                type: 'success',
                message: ''
              })
              console.info(data)
            }
          }).catch(() => {
            this.$message.error('')
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    filters: {
      mobileFilter (val) {
        switch (val) {
          case 0:
            return ''
          case 1:
            return ''
        }
        return val
      }
    }
  }
</script>

<style lang="less" scoped>
  .mul-col {
    height: auto !important;
    border: 0px !important;
  }

  .flex-row {
    display: flex;
  }

  .info-label {
    width: 70px;
  }

  .info-detail {
    width: ~"calc(100% - 70px)";
  }
  .jf-icon{
    float: right;
    line-height: 24px;
    cursor: pointer;
  }

  .input__inner {
    width: 237px;
  }
</style>



