import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'
/* // page */
import Login from '@/views/login'
import Index from '@/views/index'
import redirect from '@/views/redirect'
import PersonalQueue from '@/views/personalQueue' // 
import CaseDetail from '@/views/caseDetail' // 
/*  */
import BeachSearch from '@/views/search/beachSearch' // 
import WorkQualitySearch from '@/views/search/workQualitySearch' // 
import ColligateSearch from '@/views/search/colligateSearch' // 

import InboundSearch from '@/views/search/inboundSearch' // inbound
/*  */
import ManualEntrust from '@/views/manualEntrust' // 
import PersonSearch from '@/views/search/personSearch' // 
import DebitApproval from '@/views/debitApproval' // 
/*  */
import personAnalysis from '@/views/statistics/personAnalysis' // 
import dataStatistics from '@/views/statistics/dataStatistics' // 
import ClassTable from '@/views/classTable' // 
import Report from '@/views/report' // 

import Home from '@/views/home'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      component: Index,
      // meta: { requireAuth: true },
      children: [
        {
          path: '',
          name: '',
          component: Home,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'personalQueue',
          name: '',
          component: PersonalQueue,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'refresh',
          name: '',
          component: redirect,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'caseDetail',
          name: '',
          component: CaseDetail,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'beachSearch',
          name: '',
          component: BeachSearch,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'inboundSearch',
          name: 'inbound',
          component: InboundSearch,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'colligateSearch',
          name: '',
          component: ColligateSearch,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'manualEntrust',
          name: '',
          component: ManualEntrust,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'personSearch',
          name: '',
          component: PersonSearch,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'workQualitySearch',
          name: '',
          component: WorkQualitySearch,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'classTable',
          name: '',
          component: ClassTable,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'report',
          name: '',
          component: Report,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'debitApproval',
          name: '',
          component: DebitApproval,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'personAnalysis',
          name: '',
          component: personAnalysis,
          meta: {
            keepAlive: true // 
          }
        }, {
          path: 'dataStatistics',
          name: '',
          component: dataStatistics,
          meta: {
            keepAlive: true // 
          }
        }
      ]
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
  // mode: 'history',
  // scrollBehavior (to, from, savedPosition) {
  //   //  # 
  //   // to.hash ()
  //   //  #3
  //   if (to.hash) {
  //     return {
  //       //  to.hash 
  //       //  html 
  //       //  #3  querySelector('#3') id = 3 
  //       selector: to.hash
  //     }
  //   }
  // }
})

router.beforeEach((to, from, next) => {
  if (to.path === '/refresh') {
    store.commit('SET_EXCLUDE_ROUTER', from.path)
    next()
  } else {
    // let exTabs = store.state.tabs.excludeTab
    // if (exTabs.findIndex(val => { return val.url === to.path }) > -1) {
    //   console.info('before')
    //   store.commit('SET_EXCLUDE_ROUTER', to.path)
    // } else {
    // }
    next()
  }
  // if (to.matched.some(r => r.meta.requireAuth)) {
  // console.log('this page need login')
  // if (localStorage.getItem('token')) {
  //   next()
  // } else {
  //   next({
  //     path: '/login',
  //     query: {
  //       redirect: to.fullPath
  //     }
  //   })
  // }
  // } else {
  //   next()
  // }
})
router.afterEach((to, from) => {
  let exTabs = store.state.tabs.excludeTab
  let exIdx = exTabs.findIndex(val => {
    return val.url === to.path
  })
  if (exIdx > -1) {
    exTabs.splice(exIdx, 1)
    store.commit('SET_EX_TAB', exTabs)
    store.commit('SET_KEEP', false)
  }
  store.dispatch('newTab', {url: to.path, name: to.name})
})

export default router



