<template>
  <section class="manualEntrust">
    <el-row class="breadcrumb_nav" type="flex" justify="space-between">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <div>
        <el-button size="mini" class="search_nav_btn search_btn" @click="search" id="search"></el-button>
        <clear-btn :formObj="form" :formRef="'manualEntrustForm'"></clear-btn>
      </div>
    </el-row>
    <div class="search_nav">
      <el-form ref="manualEntrustForm" id="manualEntrustForm" v-model="form" label-width="100px">
        <el-row class="comment-search-nav">
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.scene" placeholder="">
                <el-option value="" label="">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.product" placeholder="">
                <el-option value="" label="">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.businessCent" placeholder="">
                <el-option value="" label="">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label=":">
              <el-input size="small" v-model="form.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-input size="small" v-model="form.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-input size="small" v-model="form.idCode"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item prop="rushStart" label=":">
              <el-date-picker
                size="small"
                :editable=false
                v-model="form.rushTime"
                type="daterange"
                placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.caseLevel" placeholder="">
                <el-option value="0" label="M0">
                </el-option>
                <el-option value="1" label="M1">
                </el-option>
                <el-option value="2" label="M2">
                </el-option>
                <el-option value="3" label="M3">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.caseStatus" placeholder="">
                <el-option value="0" label="">
                </el-option>
                <el-option value="1" label="">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.caseIsOld" placeholder="">
                <el-option value="0" label="">
                </el-option>
                <el-option value="1" label="">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":">
              <el-select size="small" v-model="form.mergeCaseNum" placeholder="">
                <el-option value="0" label="M0">
                </el-option>
                <el-option value="1" label="M1">
                </el-option>
                <el-option value="2" label="M2">
                </el-option>
                <el-option value="3" label="M3">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-row class="table_nav">
      <el-table
        :data="tableData"
        max-height="600"
        @selection-change="handleSelectionChange"
        style="width: 100%">
        <el-table-column
          type="selection"
          width="80">
        </el-table-column>
        <el-table-column
          prop="customerName"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          prop="phone"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          prop="idCode"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="merchant"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          prop="amountDue"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          prop="alreadyPaidAmound"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="rushTIme"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="caseLevel"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          prop="collectionCompany"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          prop="collectPerson"
          label=""
          width="150">
          <template slot-scope="scope">
            <span :class="{assistPerson : scope.row.isAssist}">{{scope.row.collectPerson}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="caseStatus"
          label=""
          width="150">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="100">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="detail(scope.row.id)"></el-button>
            <el-button type="text" size="small" @click="entrust(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end">
        <div class="entrust_btn_group">
          <el-button></el-button>
          <el-button></el-button>
        </div>
      </el-row>
      <div class="jfPagination">
        <el-row type="flex" justify="end">
          <el-pagination
            @current-change="handlePageChange"
            layout="total,prev, pager, next"
            :size="10"
            :page-size="20"
            :current-page.sync="currentPage"
            :total="totalPage">
          </el-pagination>
        </el-row>
      </div>
    </el-row>
    <el-dialog :modal="needBack" size="mini" title="" :visible.sync="entrustModelVisible">
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.person" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.type" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.time" placeholder="">
            <el-option label="1" value="0"></el-option>
            <el-option label="2" value="1"></el-option>
            <el-option label="3" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-row type="flex" justify="center" class="dialog-footer">
        <el-button @click="entrustModelVisible = false"> </el-button>
        <el-button type="primary" @click="entrustModelVisible = false"></el-button>
      </el-row>
    </el-dialog>
  </section>
</template>

<script>
  import clearBtn from '@/components/clearBtn'
  import api from '@/utils/api'
  export default {
    data () {
      return {
        form: {
          name: '',
          scene: '', // 
          phone: '',
          idCode: '',
          caseLevel: '',
          rushStartTime: '', // 
          rushEndTime: '',
          rushEndTim: '',
          caseStatus: '', // 
          collectionCompany: '', // 
          collectionPerson: '', // 
          mergeCaseNum: ''
        },
        caseIsOld: '1',
        currentPage: 2,
        totalPage: 100,
        tableData: [{
          check: true,
          customerName: '',
          phone: '13917379649',
          idCode: '350303********1510',
          merchant: '',
          mergeNum: '12',
          rushTime: '2017-12-25',
          caseLevel: 'M6+',
          collectionCompany: '',
          collectPerson: '',
          caseStatus: '',
          isAssist: true
        }],
        selectedOption: [],
        entrustModelVisible: false,
        needBack: false
      }
    },
    components: {
      clearBtn
    },
    mounted () {
      this.$store.dispatch('initForm', this.$refs['manualEntrustForm']) // storeform
      this.getSearchData()
    },
    methods: {
      handlePageChange (val) {
        this.getSearchData(val)
      },
      handleSelectionChange (val) {
        console.info(val)
      },
      entrust (id, name) {
        this.entrustModelVisible = true
      },
      detail () {
        this.$router.push({path: '/personalQueue'})
      },
      search () {
        // validate the form data
        this.getSearchData()
      },
      getSearchData (page) {
        api.manualEntrustSearch({
          form: page,
          data: this.form
        }).then(response => {
          let data = response.data
          console.info(data)
        }).catch(error => {
          if (error) {
          }
        })
      }
    }
  }
</script>

<style lang="less">
  .manualEntrust {
    .entrust_btn_group {
      margin: 4px 0;
    }
  }
</style>



