<script>
  export default {
    render (createElement) {
      if (this.row.status === 0) {
        return createElement('div', {}, [
          this.createButton(createElement, this.agree, ''),
          this.createButton(createElement, this.refuse, '')
        ])
      } else {
        return createElement('div', {
          style: {
            textAlign: 'center'
          },
          // DOM 
          domProps: {
            innerHTML: this.type[this.row.status]
          }
        })
      }
    },
    methods: {
      createButton (createElement, func, innerHtml) {
        return createElement('el-button', {
          props: {
            type: 'text',
            size: 'small'
          },
          domProps: {
            innerHTML: innerHtml
          },
          on: {
            click: func
          }
        })
      },
      agree (id) {
        console.info('')
      },
      refuse (id) {
        console.info('')
      }
    },
    data () {
      return {
        type: {
          '1': '',
          '2': '',
          '3': '',
          '4': ''
        }
      }
    },
    props: ['row']
  }
</script>

<style lang="less">

</style>



